<template>
  <div class="wrapper">
    <img
      class="wrapper__img"
      src="http://www.dell-lee.com/imgs/vue3/user.png"
      alt=""
    />
    <div class="wrapper__input">
      <input
        class="wrapper__input__content"
        type="text"
        placeholder="请输入用户名"
        v-model="account"
      />
    </div>
    <div class="wrapper__input">
      <input
        class="wrapper__input__content"
        type="phone"
        placeholder="请再次输入电话"
        v-model="mobile"
      />
    </div>
    <div class="wrapper__input">
      <input
        class="wrapper__input__content"
        type="email"
        placeholder="请再次输入电子邮箱"
        v-model="email"
      />
    </div>
    <div class="wrapper__input">
      <input
        class="wrapper__input__content"
        type="password"
        placeholder="请输入密码"
        v-model="password"
      />
    </div>
    <div class="wrapper__register-buttom" @click="handleRegister()">
      立即注册
    </div>
    <div class="wrapper__register-link" @click="handleLogin()">
      已有账号去登录
    </div>
  </div>
  <Toast v-if="show" :message="toastMsg" />
</template>

<script>
import { reactive, toRefs } from 'vue'
import { useRouter } from 'vue-router'
import Toast, { useToastEffect } from '../../components/Toast'
import { post } from '../../utils/request'
const useRegisterEffect = (showToast) => {
  const router = useRouter()
  const data = reactive({
    account: '',
    password: '',
    email: '',
    mobile: ''
  })
  const handleRegister = async () => {
    try {
      const result = await post('user/register', {
        username: data.account,
        password: data.password,
        email: data.email,
        mobile: data.mobile
      })
      if (result.code === 1) {
        showToast(result.msg)
        setTimeout(() => {
          router.push({ name: 'Login' })
        }, 3000)
      } else {
        showToast(result.msg)
      }
    } catch (e) { }
    showToast('请求失败')
  }
  const { account, password, email, mobile } = toRefs(data)
  return { account, password, email, mobile, handleRegister }
}
export default {
  name: 'Register',
  components: { Toast },
  setup() {
    const router = useRouter()
    const { show, toastMsg, showToast } = useToastEffect()
    const { account, password, email, mobile, handleRegister } = useRegisterEffect(showToast)

    const handleLogin = () => {
      const { isLogin } = localStorage
      isLogin ? router.push({ name: 'Home' }) : router.push({ name: 'Login' })
    }
    return { account, password, email, mobile, show, toastMsg, handleRegister, handleLogin }
  }
}
</script>
<style lang="scss" scoped>
@import "../../style/viriables.scss";
.wrapper {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
  &__img {
    display: block;
    width: 0.66rem;
    height: 0.66rem;
    margin: 0 auto 0.4rem auto;
  }
  &__input {
    background: #f9f9f9;
    border: 1px solid rgb(0, 0, 0, 0.1);
    border-radius: 6px;
    height: 0.48rem;
    margin: 0 0.4rem 0.16rem 0.4rem;
    padding: 0 0.16rem;
    &__content {
      width: 100%;
      line-height: 0.48rem;
      border: none;
      outline: none;
      background: none;
      color: $content-notice-fontcolor;
      &::placeholder {
        font-size: 0.16rem;
        color: $content-notice-fontcolor;
      }
    }
  }
  &__register-buttom {
    margin: 0.32rem 0.4rem 0.16rem 0.32rem;
    background: #0091ff;
    box-shadow: 0 0.04rem 0.08rem 0 rgb(0, 145, 255, 0.32);
    border-radius: 0.04rem;
    border-radius: 0.04rem;
    color: #fff;
    line-height: 0.48rem;
    font-size: 0.16rem;
    text-align: center;
  }
  &__register-link {
    text-align: center;
    color: $content-notice-fontcolor;
    font-size: 0.14rem;
  }
}
</style>
