<template>
  <div class="wrapper">
    <img
      class="wrapper__img"
      src="http://www.dell-lee.com/imgs/vue3/user.png"
      alt=""
    />
    <div class="wrapper__input">
      <input
        class="wrapper__input__content"
        type="text"
        placeholder="请输入用户名"
        v-model="account"
      />
    </div>
    <div class="wrapper__input">
      <input
        class="wrapper__input__content"
        type="password"
        placeholder="请输入密码"
        v-model="password"
      />
    </div>
    <div class="wrapper__login-buttom" @click="handleLogin()">登录</div>
    <div class="wrapper__login-link" @click="handleRegister()">立即注册</div>
  </div>
  <Toast v-if="show" :message="toastMsg" />
</template>

<script>
import { useRouter } from 'vue-router'
import { reactive, toRefs } from 'vue'
import { post } from '../../utils/request'
import Toast, { useToastEffect } from '../../components/Toast'

// 登录模块流程控制
const useLoginEffect = (showToast) => {
  const inputData = reactive({
    account: 'steven',
    password: 'qwe123'
  })
  const router = useRouter()
  const handleLogin = async () => {
    try {
      const result = await post('user/login', {
        account: inputData.account,
        password: inputData.password
      })
      console.log(result)
      if (result.code === 200) {
        localStorage.isLogin = true
        router.push({ name: 'Home' })
      } else {
        showToast(result.msg)
      }
    } catch (e) {
      showToast('请求失败')
    }
  }
  const { account, password } = toRefs(inputData)
  return { account, password, handleLogin }
}

// 注册跳转
const useRegisterEffect = () => {
  const router = useRouter()
  const handleRegister = () => {
    router.push({ name: 'Register' })
  }
  return { handleRegister }
}

export default {
  name: 'Login',
  components: { Toast },
  setup() {
    const { show, toastMsg, showToast } = useToastEffect()
    const { account, password, handleLogin } = useLoginEffect(showToast)
    const { handleRegister } = useRegisterEffect()
    return {
      account,
      password,
      show,
      toastMsg,
      handleLogin,
      handleRegister
    }
  }
}
</script>
<style lang="scss" scoped>
@import "../../style/viriables.scss";
.wrapper {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
  &__img {
    display: block;
    width: 0.66rem;
    height: 0.66rem;
    margin: 0 auto 0.4rem auto;
  }
  &__input {
    background: #f9f9f9;
    border: 1px solid rgb(0, 0, 0, 0.1);
    border-radius: 6px;
    height: 0.48rem;
    margin: 0 0.4rem 0.16rem 0.4rem;
    padding: 0 0.16rem;
    &__content {
      width: 100%;
      line-height: 0.48rem;
      border: none;
      outline: none;
      background: none;
      color: $content-notice-fontcolor;
      &::placeholder {
        font-size: 0.16rem;
        color: $content-notice-fontcolor;
      }
    }
  }
  &__login-buttom {
    margin: 0.32rem 0.4rem 0.16rem 0.32rem;
    background: #0091ff;
    box-shadow: 0 0.04rem 0.08rem 0 rgb(0, 145, 255, 0.32);
    border-radius: 0.04rem;
    border-radius: 0.04rem;
    color: #fff;
    line-height: 0.48rem;
    font-size: 0.16rem;
    text-align: center;
  }
  &__login-link {
    text-align: center;
    color: $content-notice-fontcolor;
    font-size: 0.14rem;
  }
}
</style>
